import React, { createRef, useEffect } from 'react';
import Heading from '@elements/heading';
import useMousePointer from '@hooks/useMousePointer';
import styles from './main-section.module.scss';

const MainSection = () => {
  const headingRef = createRef<HTMLElement>();
  const { isPointerInElement, mousePosition, setRef } = useMousePointer();
  useEffect(() => {
    isPointerInElement(headingRef);
  }, [mousePosition.x, mousePosition.y]);

  return (
    <div className={styles.stl}>
      <Heading ref={setRef} className={styles.stl__heading} tag="h1">
        Jonatán Isaac
        <br />
        Treviño Galindo
      </Heading>
    </div>
  );
};

export default MainSection;
